import react, { useState } from "react";
import "./App.css";
import pic from "./image/e.jpg";
import pic2 from "./image/a.jpg";

function App() {
  const [img, setImg] = useState(false);
  const handleChange = () => {
    setImg((img) => !img);
  };
  console.log(img);

  return (
    <div className="App">
      <h1>My first page</h1>
      <img src={` ${img ? pic : pic2}`} alt="p" />
      <button onClick={handleChange}>Click to Change</button>
    </div>
  );
}

export default App;
